<template>
    <section>

        <nav :class="{
            navbar: true,
            'navbar-expand-lg': true,
            'position-fixed': true,
            'w-100': true,
            'text-uppercase': true, 
            small: true,
            'd-none': true,
            'd-md-flex': true,
            'bg-transparent': !isScroll,
            'bg-black': isScroll 
        }">
            <div class="container">
                <ul class="navbar-nav mx-auto">
                    <li class="nav-item">
                        <a class="nav-link" href="#aboutme" v-smooth-scroll @click="trackEvent('aboutme')">About Me</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="#skills" v-smooth-scroll @click="trackEvent('skills')">Skills</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="#works" v-smooth-scroll @click="trackEvent('works')">Work</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="#repositories" v-smooth-scroll @click="trackEvent('repositories')">Repositories</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="#certifications" v-smooth-scroll @click="trackEvent('certifications')">Certifications</a>
                    </li>
                </ul>
            </div>
        </nav>

        <nav class="nav-mobile position-fixed w-100 d-md-none">
            <div @click="isOpen = !isOpen" :class="{
                'open-main-nav': true,
                'is-open': isOpen
            }">
                <span class="burger"></span>
                <span class="burger-text">Menu</span>
            </div>
            <div :class="{
                drawer: true,
                animated: true,
                'd-none': !isOpen,
                'd-flex': isOpen,
                fadeIn: isOpen,
                fadeOut: !isOpen,
                fast: true
            }">
                <div class="align-self-center px-4 w-100 text-right">
                    <a class="d-block text-uppercase h4 font-weight-bold" href="#aboutme" v-smooth-scroll @click="trackEvent('aboutme', true)">About Me</a>
                    <a class="d-block text-uppercase h4 font-weight-bold" href="#skills" v-smooth-scroll @click="trackEvent('skills', true)">Skills</a>
                    <a class="d-block text-uppercase h4 font-weight-bold" href="#works" v-smooth-scroll @click="trackEvent('works', true)">Work</a>
                    <a class="d-block text-uppercase h4 font-weight-bold" href="#repositories" v-smooth-scroll @click="trackEvent('repositories', true)">Repositories</a>
                    <a class="d-block text-uppercase h4 font-weight-bold" href="#certifications" v-smooth-scroll @click="trackEvent('certifications', true)">Certifications</a>
                </div>
            </div>
        </nav>

    </section>
</template>

<script>
    export default {
        data() {
            return {
                isOpen: false,
                isScroll: false
            }
        },
        methods: {
            handleScroll () {
                if (window.scrollY > 0 ) {
                    this.isScroll = true;
                } else {
                    this.isScroll = false;
                }
            },
            trackEvent(page, mobile = false) {
                this.$ga.event('anchor', 'clic', page)
                if (mobile) {
                    this.isOpen = !this.isOpen;
                }
            }
        },
        mounted() {
            window.addEventListener('scroll', this.handleScroll);
        }
    }
</script>

<style scoped>
    .nav-item {
        padding: .5rem 1.5rem!important;
    }

    .nav-link {
        padding:0!important;
        color:#ffcb08;
        position:relative;
    }

    .nav-link::before {
        content: '';
        transition: all .7s cubic-bezier(.165,.84,.44,1);
        position: absolute;
        bottom: -.11759rem;
        height: .11759rem;
        background-color: #252427;
        width: 0%;
        left: 0;
    }

    .nav-link:hover::before {
        width:100%;
        background-color: #ffcb08;
    }

    .nav-mobile {
        z-index: 999;
    }

    .open-main-nav {
        position: absolute;
        top: 20px;
        padding-top: 20px;
        right: 20px;
        z-index: 1000;
        background: none;
        border: 0;
        cursor: pointer;
    }

    .open-main-nav:focus {
        outline: none;
    }

    .burger {
        position: relative;
        display: block;
        width: 28px;
        height: 4px;
        margin: 0 auto;
        background: #ffcb08;
        transform: skew(5deg);
        transition: all .275s;
    }

    .burger:after,
    .burger:before {
        content: '';
        display: block;
        height: 100%;
        background: #ffcb08;
        transition: all .275s;
    }

    .burger:after {
        transform: translateY(-12px) translateX(-2px) skew(-20deg);
    }

    .burger:before {
        transform: translateY(-16px) skew(-10deg);
    }

    .open-main-nav.is-open .burger {
        transform: skew(5deg) translateY(-8px) rotate(-45deg);
    }

    .open-main-nav.is-open .burger:before {
        transform: translateY(0px) skew(-10deg) rotate(75deg);
    }

    .open-main-nav.is-open .burger:after {
        transform: translateY(-12px) translateX(10px) skew(-20deg);
        opacity: 0;
    }

    .burger-text {
        display: block;
        font-size: .675rem;
        letter-spacing: .05em;
        margin-top: .5em;
        text-transform: uppercase;
        font-weight: 500;
        text-align: center;
        color: #ffcb08;
    }

    .drawer {
        position:fixed;
        height: 100vh;
        background-color:#333;
        width:100%;
    }

    .drawer a {
        color: #ffcb08;
    }
</style>
