<template>
    <div>
        
        <intro />
        <div class="overlay" v-bind:style="{
            borderLeftWidth: borderLeftWidthOverlay + 'px'
        }"></div>
        <about-me id="aboutme" />
        <skills id="skills" />
        <works id="works" />
        <repositories id="repositories" />
        <certifications id="certifications" />
        <div class="triangle-down" :style="{
            borderLeftWidth: borderDownWidthOverlay + 'px',
            borderRightWidth: borderDownWidthOverlay + 'px'
        }"></div>
        <contact />

    </div>
</template>

<script>
    import Intro from '../components/Home/Intro.vue'
    import AboutMe from '../components/Home/AboutMe.vue'
    import Skills from '../components/Home/Skills.vue'
    import Works from '../components/Home/Works.vue'
    import Repositories from '../components/Home/Repositories.vue'
    import Certifications from '../components/Home/Certifications.vue'
    import Contact from '../components/Home/Contact.vue'
    import AOS from 'aos'

    export default {
        components: { Intro, AboutMe, Skills, Works, Repositories, Certifications, Contact },
        data() {
            return {
                borderLeftWidthOverlay: 0,
                borderDownWidthOverlay: 0
            }
        },
        mounted() {
            this.borderLeftWidthOverlay = window.innerWidth - 50;
            this.borderDownWidthOverlay = window.innerWidth / 2;
            AOS.init()
        }
    }
</script>

<style scoped>
    .overlay{
        z-index: 500;
        width: 0;
        height: 0;
        position: absolute;
        bottom: -20px;
        border-style: solid solid solid dashed;
        border-top-width: 100px;
        border-left-width: 0px;
        border-color: transparent #fff;
        border-bottom-width: 0;
        border-right-width: 0;
    }

    @media screen and (max-width: 767px) {
        .overlay {
            bottom: -60px;
        }
    }

    #aboutme, #skills, #repositories {
        scroll-behavior: smooth;
    }

    .triangle-down { 
        width: 0; 
        height: 0; 
        border-left: 50px solid #ffcb08; 
        border-right: 50px solid #ffcb08; 
        border-top: 50px solid #fff; 
    }
</style>

