<template>
    <section class="both py-5 bg-repositories bg-light" :style="{ backgroundImage: 'url(' + githubBackground + ')' }"> 
        <div class="container py-5">
            <h2 class="text-uppercase text-center mb-5" data-aos="fade-in" data-aos-once="true">
                Github
                <span class="d-block text-yellow">Repositories</span>
            </h2>
            <div class="row">
                <div class="col-12 col-md-6" v-for="(repository, index) in repositories" :key="index">
                    <div class="bg-white border rounded p-3 mb-3 shadow-sm" data-aos="fade-in" data-aos-duration="1000" data-aos-delay="50" data-aos-once="true">
                        <div class="d-flex flex-column"> 
                            <div class="mb-auto">
                                <a class="font-weight-bold" :href="repository.html_url"><i class="fas fa-code-branch mr-2"></i>{{ repository.name }}</a>
                                <p class="small text-muted text-truncate">{{ repository.description }}</p>
                            </div> 
                            <div class="small">
                                <div class="d-flex justify-content-between">
                                    <div>
                                        <span class="badge-language" :style="{ backgroundColor: color[repository.language] }"></span>{{ repository.language }}
                                        <span class="ml-3 text-secondary"><i class="fas fa-star mr-1"></i>{{ repository.stargazers_count }}</span>
                                    </div>
                                    <div class="text-muted">{{ formatDate(repository.updated_at) }}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row mt-5" data-aos="fade-in" data-aos-once="true" data-aos-duration="1000" data-aos-delay="200">
                <div class="col text-center">
                    <a href="https://github.com/davidecesarano/" class="btn btn-outline-primary"><i class="fab fa-github mr-2"></i>Mostra altri</a>
                </div>
            </div>
        </div>
    </section>
</template> 

<script>
    import axios from 'axios'
    import GitHubBg from '../../assets/images/features-hero-code-review.svg'
    
    export default {
        data() {
            return {
                repositories: [],
                color: {
                    PHP: '#4F5D95',
                    Shell: '#89e051',
                    JavaScript: '#ffcb08'
                },
                githubBackground: GitHubBg
            }
        },
        methods: {
            formatDate(updatedAt){
                var months = ['Gen', 'Feb', 'Mar', 'Apr', 'Mag', 'Giu', 'Lug', 'Ago', 'Set', 'Ott', 'Nov', 'Dic'];
                var date = new Date(updatedAt);
                return 'Updated '+date.getDay()+' '+months[date.getMonth()];
            }
        },
        mounted() {
            axios.get('https://api.github.com/users/davidecesarano/repos?sort=updated&direction=desc&per_page=4')
                .then(response => {
                    this.repositories = response.data;
                })
                .catch(function (error) {
                    console.log(error);
                })
        }
    }
</script>

<style scoped>
    .container {
        max-width: 960px!important;
    }

    .bg-repositories {
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
    }

    .border {
        border-width: 2px!important;
    }

    .badge-yellow {
        border: 1px solid #ffcb08;
        background-color:transparent;
        color: #ffcb08;
        font-weight: normal;
    }

    .badge-language{
        width: 12px;
        height: 12px;
        display: inline-block;
        border-radius: 100%;
        margin-right: 5px;
        position: relative;
        top: 1px;
    }

    .btn-outline-primary{
        color: #333!important;
    }

    a {
        color:#212529;
    }

    a:hover {
        text-decoration: none;
        color:#ffcb08;
        transition: all 0.5s;
    }
</style>